import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useUser } from '../context/UserContext';
import ImageUploader from './ImageUploader';
import { ArrowLeft, Loader2, AlertCircle, CheckCircle } from 'lucide-react';

const CropDiagnosis = () => {
  const { language } = useUser();
  const navigate = useNavigate();
  const [imageFile, setImageFile] = useState(null);
  const [previewUrl, setPreviewUrl] = useState('');
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [result, setResult] = useState(null);
  const [error, setError] = useState('');

  const translations = {
    english: {
      title: 'Crop Health Diagnosis',
      subtitle: 'Upload an image of your crop for analysis',
      analyzeButton: 'Analyze Image',
      backButton: 'Back to Dashboard',
      loading: 'Analyzing your crop...',
      resultTitle: 'Analysis Results',
      issue: 'Issue',
      confidence: 'Confidence',
      recommendations: 'Recommendations',
      noImage: 'Please upload an image to analyze',
      error: 'Error analyzing image. Please try again.'
    },
    hindi: {
      title: 'फसल स्वास्थ्य निदान',
      subtitle: 'विश्लेषण के लिए अपनी फसल की एक छवि अपलोड करें',
      analyzeButton: 'छवि का विश्लेषण करें',
      backButton: 'डैशबोर्ड पर वापस जाएं',
      loading: 'आपकी फसल का विश्लेषण किया जा रहा है...',
      resultTitle: 'विश्लेषण परिणाम',
      issue: 'समस्या',
      confidence: 'विश्वसनीयता',
      recommendations: 'सिफारिशें',
      noImage: 'कृपया विश्लेषण के लिए एक छवि अपलोड करें',
      error: 'छवि विश्लेषण में त्रुटि। कृपया पुनः प्रयास करें।'
    },
    marathi: {
      title: 'पीक आरोग्य निदान',
      subtitle: 'विश्लेषणासाठी आपल्या पिकाची प्रतिमा अपलोड करा',
      analyzeButton: 'प्रतिमेचे विश्लेषण करा',
      backButton: 'डॅशबोर्डवर परत जा',
      loading: 'तुमच्या पिकाचे विश्लेषण केले जात आहे...',
      resultTitle: 'विश्लेषण परिणाम',
      issue: 'समस्या',
      confidence: 'विश्वास',
      recommendations: 'शिफारसी',
      noImage: 'कृपया विश्लेषणासाठी एक प्रतिमा अपलोड करा',
      error: 'प्रतिमा विश्लेषणात त्रुटी. पुन्हा प्रयत्न करा.'
    },
    gujarati: {
      title: 'ફસલ આરોગ્ય નિદાન',
      subtitle: 'વિશ્લેષણ માટે તમારી ફસલની છબી અપલોડ કરો',
      analyzeButton: 'છબીનું વિશ્લેષણ કરો',
      backButton: 'ડેશબોર્ડ પર પાછા જાઓ',
      loading: 'તમારી ફસલનું વિશ્લેષણ કરવામાં આવી રહ્યું છે...',
      resultTitle: 'વિશ્લેષણ પરિણામો',
      issue: 'સમસ્યા',
      confidence: 'વિશ્વાસ',
      recommendations: 'ભલામણો',
      noImage: 'કૃપા કરીને વિશ્લેષણ કરવા માટે એક છબી અપલોડ કરો',
      error: 'છબીનું વિશ્લેષણ કરવામાં ભૂલ. કૃપા કરીને ફરી પ્રયાસ કરો.'
    }
  };

  const t = translations[language] || translations.english;

  const handleImageUpload = (file, preview) => {
    setImageFile(file);
    setPreviewUrl(preview);
    setResult(null);
    setError('');
  };

  const handleRemoveImage = () => {
    setImageFile(null);
    setPreviewUrl('');
    setResult(null);
  };

  const analyzeImage = async () => {
    if (!imageFile) {
      setError(t.noImage);
      return;
    }

    setIsAnalyzing(true);
    setError('');

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // Mock response
      const mockResponse = {
        diagnosis: [
          {
            issue: language === 'hindi' ? 'पत्ती का धब्बा रोग' : 
                   language === 'marathi' ? 'पानावर ठिपके' : 
                   language === 'gujarati' ? 'પાંદડા પર ડાઘ' : 'Leaf Spot Disease',
            confidence: 0.87,
            description: language === 'hindi' ? 'पत्तियों पर भूरे रंग के धब्बे दिखाई दे रहे हैं' :
                          language === 'marathi' ? 'पानावर तपकिरी डाग दिसत आहेत' :
                          language === 'gujarati' ? 'પાંદડા પર ભુખરા ડાઘ દેખાય છે' :
                          'Brown spots appearing on leaves',
            recommendations: [
              language === 'hindi' ? 'प्रभावित पत्तियों को हटा दें' :
              language === 'marathi' ? 'प्रभावित पाने काढून टाका' :
              language === 'gujarati' ? 'અસરગ્રસ્ત પાંદડા દૂર કરો' :
              'Remove affected leaves',
              language === 'hindi' ? 'कवकनाशी का प्रयोग करें' :
              language === 'marathi' ? 'फंगीसायड वापरा' :
              language === 'gujarati' ? 'ફંગિસાઇડનો ઉપયોગ કરો' :
              'Apply fungicide',
              language === 'hindi' ? 'पौधों के बीच उचित वायु संचार सुनिश्चित करें' :
              language === 'marathi' ? 'वनस्पतींमध्ये योग्य हवा वाहतूक सुनिश्चित करा' :
              language === 'gujarati' ? 'છોડવાઓ વચ્ચે યોગ્ય હવાનું વહન સુનિશ્ચિત કરો' :
              'Ensure proper air circulation between plants'
            ]
          }
        ]
      };

      setResult(mockResponse);
    } catch (err) {
      console.error('Error analyzing image:', err);
      setError(t.error);
    } finally {
      setIsAnalyzing(false);
    }
  };

  return (
    <div className="max-w-4xl mx-auto p-4">
      <div className="flex items-center mb-6">
        <button
          onClick={() => navigate(-1)}
          className="flex items-center text-gray-600 hover:text-gray-800 mr-4"
        >
          <ArrowLeft className="w-5 h-5 mr-1" />
          {t.backButton}
        </button>
      </div>

      <div className="bg-white rounded-lg shadow-md p-6 mb-6">
        <h1 className="text-2xl font-bold text-gray-800 mb-2">{t.title}</h1>
        <p className="text-gray-600 mb-6">{t.subtitle}</p>

        <div className="mb-6">
          <ImageUploader
            onImageUpload={handleImageUpload}
            onRemoveImage={handleRemoveImage}
            previewUrl={previewUrl}
            className="w-full h-64"
          />
        </div>

        {error && (
          <div className="bg-red-50 border-l-4 border-red-500 p-4 mb-6">
            <div className="flex">
              <div className="flex-shrink-0">
                <AlertCircle className="h-5 w-5 text-red-500" />
              </div>
              <div className="ml-3">
                <p className="text-sm text-red-700">{error}</p>
              </div>
            </div>
          </div>
        )}

        <button
          onClick={analyzeImage}
          disabled={isAnalyzing || !imageFile}
          className={`w-full bg-green-600 text-white py-2 px-4 rounded-md hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-green-500 focus:ring-offset-2 ${(!imageFile || isAnalyzing) ? 'opacity-50 cursor-not-allowed' : ''}`}
        >
          {isAnalyzing ? (
            <div className="flex items-center justify-center">
              <Loader2 className="animate-spin h-5 w-5 mr-2" />
              {t.loading}
            </div>
          ) : (
            t.analyzeButton
          )}
        </button>
      </div>

      {result && (
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">{t.resultTitle}</h2>
          
          {result.diagnosis.map((item, index) => (
            <div key={index} className="mb-6 p-4 bg-gray-50 rounded-lg">
              <div className="flex justify-between items-center mb-2">
                <h3 className="text-lg font-medium text-gray-900">
                  {t.issue}: {item.issue}
                </h3>
                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                  {t.confidence}: {Math.round(item.confidence * 100)}%
                </span>
              </div>
              
              <p className="text-gray-600 mb-4">{item.description}</p>
              
              <div>
                <h4 className="font-medium text-gray-900 mb-2">{t.recommendations}:</h4>
                <ul className="list-disc pl-5 space-y-1">
                  {item.recommendations.map((rec, i) => (
                    <li key={i} className="text-gray-600">{rec}</li>
                  ))}
                </ul>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default CropDiagnosis;
